.headeroption {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  color: gray;
  cursor: pointer;
}
.headeroption:hover {
  color: black;
}
.headeroption__title {
  font-size: 12px;
  font-weight: 400;
}

.headeroption__icon {
  object-fit: contain;
  height: 26px !important;
  width: 26px !important;
}
